<template>
	<div>
		<div class="btn">
			<el-button type="primary" icon="el-icon-delete" @click="delSelection()" :disabled="disabledDel">全部删除</el-button>
			<el-button type="primary" @click="dialogBanner = true">添加轮播图</el-button>
		</div>
		<el-table :data="tableDate" ref="multipleTable" @selection-change="handleSelectionChange" class="aip-table">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="轮播图ID" align="center"></el-table-column>
			<el-table-column prop="href" label="缩略图" align="center">
				<template slot-scope="scope"><img :src="scope.row.href" style="max-width:180px;max-height:80px;" /></template>
			</el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalPage">
			</el-pagination>
		</div>
		

		<el-dialog :visible.sync="dialogBanner" width="460px" title="添加轮播图">
			<div style="text-align: center;">
				<el-upload action="https://api.fengniaotuangou.cn/api/upload" list-type="picture-card" ref="upload" :auto-upload="false" :limit="1" :on-success="handleAvatarSuccess" :on-exceed="handleExceed">
					<i slot="default" class="el-icon-plus"></i>
					<div slot="file" slot-scope="{file}">
							<img  class="el-upload-list__item-thumbnail" :src="file.url" alt="">
							<span class="el-upload-list__item-actions">
								<span v-if="!disabled" class="el-upload-list__item-delete" @click="handleRemove(file)">
									<i class="el-icon-delete"></i>
								</span>
							</span>
					</div>
				</el-upload>
				<div style="margin-top:20px">
					<el-button @click="upload">上传图片</el-button>
				</div>
			<!-- 	<el-upload action="https://api.fengniaotuangou.cn/api/upload" list-type="picture-card" ref="upload" :before-upload="beforeAvatarUpload" :on-success="handleAvatarSuccess" :on-exceed="handleExceed">
					<i slot="default" class="el-icon-plus"></i>
					<div slot="file" slot-scope="{file}">
							<img  class="el-upload-list__item-thumbnail" :src="file.url" alt="">
							<span class="el-upload-list__item-actions">
								<span v-if="!disabled" class="el-upload-list__item-delete" @click="handleRemove(file)">
									<i class="el-icon-delete"></i>
								</span>
							</span>
					</div>
				</el-upload> -->
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import API from '@/api/modules/banner.js'
	let banner_img;
	export default {
		data() {
			return {
				dialogBanner: false,
				tableDate: [],
				disabled: false,
				disabledDel: true,
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getBanner()
		},
		methods: {
			// 获取轮播图
			getBanner() {
				var self = this;
				API.banners(self.currentPage).then(res => {
					console.log(res.data)
					self.tableDate = res.data
					self.totalPage = res.total
				})
			},
			handleDelete(index, row) {
				console.log(index, row);
			},
			// 删除多行
			delSelection(rows) {
				if (rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.delSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			// 选择表格行
			handleSelectionChange(val) {
				this.multipleSelection = val;
				this.disabledDel = false;

				if (this.multipleSelection == '') {
					this.disabledDel = true;
				}
			},
			
			upload() {
				this.$refs.upload.submit();
			},
			handleRemove(file, fileList) { //移除图片
				console.log(file, this.fileList);
			},
			beforeAvatarUpload(file) { //文件上传之前调用做一些拦截限制
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('上传图片大小不能超过 2MB!');
				}
				return isLt2M;
			},
			handleAvatarSuccess(res, file) { //图片上传成功
				// this.imageUrl = URL.createObjectURL(file.raw);
				this.$message.success('上传成功');
				this.dialogBanner = false;
				this.getBanner()
				
			},
			handleExceed(files, fileList) { //图片上传超过数量限制
				this.$message.error('上传图片不能超过1张!');
			},
			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getBanner();
			},
		}
	}
</script>

<style lang="scss" scoped>
	.btn {
		margin-bottom: 1vw;
	}

	.el-table {
		border: 1px solid #dfe6ec;
	}

	.block {
		text-align: center;
		margin-top: 1vw;
	}

	.submit {
		margin-top: 1vw;
		text-align: right;
	}
</style>
